img, p {
  max-width: 60%;
  height: auto;
  margin-bottom: 10px;
}

p {
  font-size: 18px;
}

img {
  filter: drop-shadow(0 0 .75rem gray);
}

.container, .cat-info {
  min-width: 320px;
  max-width: 768px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 10px;
  font-family: sans-serif;
  display: flex;
}

.loader {
  width: 68px;
  height: 68px;
  filter: blur(2px);
  box-sizing: border-box;
  border: 10px solid #ff0;
  border-bottom-color: #00f;
  border-radius: 50%;
  transition: border .5s;
  animation: rotation .25s linear infinite;
  display: inline-block;
  position: absolute;
  top: 20%;
  left: calc(50% - 24px);
}

h2 {
  text-transform: capitalize;
  font-size: 36px;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

